<template>
  <div>
    <p>Diff Excel</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <p>
      
      <select v-model="import_mode">
        <option>LOCAL</option>
        <option>REMOTE</option>
      </select>
    </p>
    <p v-if="import_mode==='LOCAL'">
      <input type="file" @change="import_local_schema($event)">
    </p>
    <p v-if="import_mode==='REMOTE'">
      :
      <input v-model="remote_schema_path" type="text" placeholder="http://">
      <button @click="import_remote_schema()"></button>
    </p>
    <p>
      Excel
      <select v-model="current_schema">
        <option v-for="(value,key) in schema" :key="key">{{key}}</option>
      </select>
      <select v-if="current_schema!==null&&current_schema!==''" v-model="current_method">
        <option v-for="method in current_methods" :key="method">{{method}}</option>
      </select>
    </p>
    <div v-if="current_schema!==null&&current_schema!==''">
      <p>
        {{current_schema}} ({{Object.keys(current_diff_obj).length}})
      </p>
      <p v-for="(value,obj) in current_diff_obj" :key="value">
        {{obj}}<input type="file" @change="parseFile($event,obj)" multiple />
      </p>
    </div>
    <div v-if="Object.keys(file_name).length!==0">
      <div v-for="(value,key) in file_name" :key="key">
        <p v-if="typeof file_data[key] === 'object'">{{key}} (LOADED {{file_data[key].length}} ROWS)</p>
      </div>
    </div>
    <p>
      <button @click="diff()"></button>
      <button @click="clean_data"></button>
    </p>
    <div v-if="Object.keys(result_sum).length!==0">
      <div v-for="(value,key) in result_sum" :key="key">
        <p>{{key}} (SUM VALUE {{value.toFixed(2)}})</p>
      </div>
    </div>
    <div v-if="Object.keys(result_diff).length!==0">
      <div v-for="(value,key) in result_diff" :key="key">
        <div v-if="value.only_sum!==0&&value.only_content.length!==0">
          <hr>
          <p>{{key}} (DIFF COUNT {{value.only_content.length}})</p>
          <p>{{key}} (DIFF SUM VALUE {{value.only_sum.toFixed(2)}})</p>
          <table border="1">
            <tr>
              <th>INDEX</th>
              <th>ID</th>
              <th>VALUE</th>
            </tr>
            <tr v-for="(item,index) in value.only_content" :key="index">
              <td>{{index+1}}</td>
              <td>{{item.ID}}</td>
              <td>{{item.VALUE}}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import XLSX from 'xlsx'

export default {
  name: 'DiffExcelView',
  data () {
    return {
      import_mode: 'LOCAL',
      schema: {},
      remote_schema_path: '',
      current_schema: null,
      current_method: null,
      file_name: {},
      file_data: {},
      result_sum: {},
      result_diff: {},
      multiple_file_temp: {}
    }
  },
  computed: {
    current_methods () {
      return this.schema[this.current_schema].methods
    },
    current_diff_obj () {
      return this.schema[this.current_schema].diff_obj
    }
  },
  methods: {
    load_schema_script (t, src, innerHTML) {
      const script = document.createElement('script')
      document.getElementsByTagName('head')[0].appendChild(script)
      script.type = 'text/javascript'
      if (src !== null) {
        script.src = src
        script.onload = function () {
          if (!this.readyState || this.readyState === 'loaded' || this.readyState === 'complete') {
            script.onload = null
            if (window.outsideSchema !== null) {
              t.schema = window.outsideSchema
            }
          }
        }
      }
      if (innerHTML !== null) {
        script.innerHTML = innerHTML
        t.schema = window.outsideSchema
      }
    },
    import_local_schema (file) {
      const t = this
      const f = file.target.files[0]
      const reader = new FileReader()
      reader.onload = function (e) {
        t.load_schema_script(t, null, e.target.result)
      }
      reader.readAsText(f)
    },
    import_remote_schema () {
      const t = this
      t.load_schema_script(t, t.remote_schema_path, null)
    },
    clean_data () {
      window.location.reload()
    },
    parseFile (files, id) {
      const t = this
      const readers = []
      for (const file of files.target.files) {
        const tag = file.name.split('.')[0]
        t.file_name[id + '_' + tag] = tag
        const reader = new FileReader()
        reader.onload = function (e) {
          const data = XLSX.read(e.target.result, {
            type: 'binary'
          })
          t.file_data[id + '_' + tag] = XLSX.utils.sheet_to_json(data.Sheets[data.SheetNames[0]])
        }
        reader.readAsBinaryString(file)
        readers.push(reader)
      }
    },
    fill (t, obj, wb, wbContent, method) {
      let tempMap
      if (wbContent.single.startsWith('multiple_file') && t.multiple_file_temp[obj] !== undefined) {
        tempMap = t.multiple_file_temp[obj]
      } else {
        tempMap = new Map()
      }
      let unique
      let number
      let tempSum = 0
      t.file_data[obj + '_' + wb].filter(wbContent.field[method].filter).forEach(e => {
        number = typeof wbContent.field[method].number !== 'string' ? wbContent.field[method].number(e) : e[wbContent.field[method].number]
        unique = typeof wbContent.field[method].unique !== 'string' ? wbContent.field[method].unique(e) : e[wbContent.field[method].unique]
        tempMap.set(unique, tempMap.get(unique) === undefined ? parseFloat(number) : tempMap.get(unique) + parseFloat(number))
        tempSum = tempSum + parseFloat(number)
      })
      t.multiple_file_temp[wbContent.single.startsWith('multiple_file') ? obj : obj + '_' + wb] = tempMap
      t.result_diff[wbContent.single.startsWith('multiple_file') ? obj : obj + '_' + wb] = {
        only_sum: 0,
        only_content: []
      }
      t.result_sum[obj + '_' + wb] = tempSum
      return tempMap
    },
    diff () {
      const t = this
      const countDiffMap = new Map()
      for (const key of Object.keys(t.current_diff_obj)) {
        for (const innerKey of Object.keys(t.current_diff_obj[key])) {
          t.fill(t, key, innerKey, t.current_diff_obj[key][innerKey], t.current_method, countDiffMap)
        }
      }
      for (const key of Object.keys(t.multiple_file_temp)) {
        t.multiple_file_temp[key].forEach((v, k) => {
          countDiffMap.set(k + '_' + v, countDiffMap.get(k + '_' + v) === undefined ? 1 : countDiffMap.get(k + '_' + v) + 1)
        })
      }
      let tempArr
      countDiffMap.forEach((v, k) => {
        tempArr = k.split('_')
        if (v !== Object.keys(t.current_diff_obj).length) {
          for (const key of Object.keys(t.multiple_file_temp)) {
            if (parseFloat(t.multiple_file_temp[key].get(tempArr[0])) === parseFloat(tempArr[1])) {
              t.result_diff[key].only_sum = t.result_diff[key].only_sum + parseFloat(tempArr[1])
              t.result_diff[key].only_content.push({
                ID: tempArr[0],
                VALUE: tempArr[1]
              })
              break
            }
          }
        }
      })
    }
  }
}
</script>

